import { Node } from "./Node";
import { Camera } from "./Camera";

export class View{
    Nodes : Node<object>[] = [];
    MainCamera : Camera;

    constructor(){
        this.MainCamera = new Camera();
        this.MainCamera.RelationX = 45;
        this.MainCamera.RelationY = 45;
    }

    Run(){
        setInterval(() => {
            this.Nodes.forEach(n => n.OnUpdate());
        }, 5);
    }
}